.body {
  position: relative;
  width: 100%;
  height: 100vh;
  overflow: hidden;
}

.back-stars {
  position: absolute;
  top: -10%;
  left: -10%;
  width: 130%;
  height: 120%;
  background: url(../img/background.jpg) top / cover no-repeat;
  z-index: -1;
  animation: backStars 1s ease;
  animation-fill-mode: forwards;
  transition: all 0.1s ease;
}

.back-smoke {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 130vw;
  height: 130vh;
  background-image: url(../img/smoke.svg);
  background-size: cover;
  opacity: 0;
  z-index: -1;
  animation: backSmoke 15s linear;
}

.header {

  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 30px 120px;
  }

  &__logo {
    img {
      width: 82px;
      height: 36px;
    }
  }

  &__nav-list {
    display: flex;
    align-items: center;
  }

  &__nav-item:not(:last-child) {
    margin-right: 30px;
  }

  &__nav-item:nth-child(5) {
    margin-right: 40px;
  }

  &__nav-link {
    font: $font-nav;
    text-transform: uppercase;
    color: $color-main;
    transition: color 0.2s ease-in-out;
    z-index: 10;

    &:hover {
      color: $color-orange;
      transition: color 0.2s ease-in-out;
    }
  }
}

.nav-close {
  position: absolute;
  top: 30px;
  left: 80px;
  display: none;
  width: 30px;
  height: 30px;

  &__line {
    position: absolute;
    top: 62%;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: $color-black;
    transition: background-color 0.2s ease-in-out;

    &:first-child {
      transform: rotate(45deg);
    }
    
    &:last-child {
      transform: rotate(-45deg);
    }
  }
}

.nav-close:hover .nav-close__line {
  background-color: $color-orange;
  transition: background-color 0.2s ease-in-out;
}

.burger {
  display: none;
  flex-direction: column;
  justify-content: space-between;
  width: 30px;
  height: 20px;

  &__line {
    width: 100%;
    height: 2px;
    background-color: $color-main;
    transition: background-color 0.2s ease-in-out;
  }
}

.burger:hover .burger__line {
  background-color: $color-orange;
  transition: background-color 0.2s ease-in-out;
}

.btn-buy-tickets {
  padding: 10px 15px;
  font: $font-button;
  text-transform: uppercase;
  border-radius: 4px;
  background-color: $color-main;
  color: $color-black;
  transition: background-color 0.2s ease-in-out, color 0.2s ease-in-out;

  &:hover {
    background-color: $color-orange;
    color: $color-main;
    transition: background-color 0.2s ease-in-out, color 0.2s ease-in-out;
  }
}

@keyframes backStars {
  0% {
    background-position-x: 0;
  }

  100% {
    background-position-x: -200px;
  }
}


@keyframes backSmoke {
  0% {
    background-position-x: 200%;
    opacity: 0;
  }

  25% {
    opacity: 0.5;
  }

  50% {
    opacity: 0;
  }

  75% {
    opacity: 0.5;
  }

  100% {
    background-position-x: 0%;
    opacity: 0;
  }
}

@media (max-width:1439px) {
  
  .back-stars {
    top: 0;
    left: 0;
    width: 100vw;
  }
  
  .back-smoke {
    width: 100vw;
  }
}

@media (max-width:1280px) {

  .header {

    &__container {
      padding: 30px 80px;
      flex-direction: row-reverse;
    }
    
    &__nav {
      position: absolute;
      top: 0;
      left: 0;
      background-color: $color-main;
      opacity: 0;
      visibility: hidden;
      transform: translateX(-150%);
      z-index: 10;
      transition: opacity 0.5s ease-in-out, visibility 0.5s ease-in-out, transform 0.5s ease-in-out;
    }

    &__nav-list {
      flex-direction: column;
      align-items: flex-start;
      padding: 100px 80px;
    }

    &__nav-item:not(:last-child) {
      margin-right: 0;
      margin-bottom: 30px;
    }

    &__nav-link {
      color: $color-black;
    }
  }

  .header__nav--active {
    opacity: 1;
    visibility: visible;
    transform: translateX(0);
    transition: opacity 0.5s ease-in-out, visibility 0.5s ease-in-out, transform 0.5s ease-in-out;
  }

  .burger {
    display: flex;
  }

  .nav-close {
    display: block;
  }

  .btn-buy-tickets {
    background-color: $color-black;
    color: $color-main;
  }
}

@media (max-width:1024px) {
  .header {
    &__container {
      padding: 20px 50px;
    }

    &__nav-list {
      padding: 90px 50px;
    }
  }

  .nav-close {
    top: 20px;
    left: 50px;
  }
}

@media (max-width:768px) {
  .header {
    &__container {
      padding: 15px 30px;
    }

    &__nav-item {
      :not(:last-child) {
        margin-bottom: 25px;
      }
    }

    &__nav-link {
      font: $font-nav-768;
    }
  }
}

@media (max-width:500px) {
  .header {
    &__container {
      padding: 15px;
    }

    &__logo {
      img {
        max-width: 62px;
        object-fit: unset;
      }
    }

    &__nav {
      width: 100%;
      height: 100%;
    }

    &__nav-list {
      padding: 80px 15px;
    }

    &__nav-item:not(:last-child) {
      margin-bottom: 25px;
    }
  }

  .burger {    
    width: 25px;
    height: 16px;
  }

  .nav-close {
    top: 20px;
    left: 16px;
    width: 22px;
    height: 22px;
  }

  .btn-buy-tickets {
    position: absolute;
    bottom: 15vh;
  }
}