.hero {

  &__container {
    padding-top: 100px;
    padding-bottom: 100px;
  }

  &__content {
    position: relative;
  }

  &__subtitle {
    margin-bottom: 40px;
    padding-left: 30px;
    font: $font-subtitle;
    text-transform: uppercase;
    letter-spacing: 1px;
    color: $color-orange;
    animation: subtitle 1s ease;
    pointer-events: none;
  }

  &__title {
    font-family: $BebasNeue;
    font-size: 712px;
    line-height: 510px;
    font-weight: 700;
    letter-spacing: -7px;
    text-transform: uppercase;
    background-image: url(../img/mars.webp);
    background-position-y: bottom;
    background-size: cover;
    -webkit-background-clip: text;
    background-clip: text;
    color: transparent;
    opacity: 0;
    animation: 2s title 1s ease, backAnim 60s linear infinite;
    animation-fill-mode: forwards;
    pointer-events: none;
    -ms-background-image:none;
  }
}

.hero__title--off {
  display: none;
}

.ie-title {
  color: $color-main;
  background:transparent;
  visibility: hidden;

  &--active {
    visibility: visible;
  }
}

.vector {
  position: absolute;
  top: 0px;
  left: -4px;
  width: 10px;
  height: 102%;
  background: url(../img/vector.svg) no-repeat top center;
  background-size: 100%;
  animation: vector 1s ease;
  -ms-animation: vectorIE 1s ease;
  
}

@keyframes backAnim {
  0% {
    background-position-x: 1500px;
    transition: all 0.1s ease;
  }

  100% {
    background-position-x: 0%;
    transition: all 0.1s ease;
  }
}

@keyframes vector {
  0% {
    height: 0%;
  }

  100% {
    height: 100%;
  }
}

@keyframes vectorIE {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1.2;
  }
}

@keyframes subtitle {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes title {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: $color-main;
  opacity: 0;
  visibility: hidden;
  transform: translateY(-120%);
  transition: opacity 1s ease-in-out, visibility 1s ease-in-out, transform 1s ease-in-out;

  &__close {
    position: absolute;
    top: 10px;
    right: 0;
    display: block;
    justify-content: center;
    align-items: center;
    width: 32px;
    height: 32px;
    background-color: $color-black;
    transition: background-color 0.2s ease-in-out;

    :first-child {
      transform: rotate(45deg);
    }
    :last-child {
      transform: rotate(-45deg);
    }
  }

  &__close-line {
    position: absolute;
    top: 46%;
    left: 9px;
    width: 14px;
    height: 2px;
    background-color: #D9D9D9;
    transition: background-color 0.2s ease-in-out;
  }

  &__body {
    padding: 90px 0;
  }

  &__content {
    position: relative;
  }

  &__title {
    margin-bottom: 90px;
    max-width: 80%;
    font-family: $BebasNeue;
    font-size: 114px;
    line-height: 114px;
    letter-spacing: 3.6px;
    color: $color-black;
  }

  &__form {
    position: relative;
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    padding-bottom: 100px;
  }

  &__form-field {
    width: 100%;
    max-width: 386px;
  }

  &__form-input-title {
    display: inline-block;
    margin-bottom: 9px;
    font: $font-nav;
    font-weight: 700;
    color: $color-black;
  }

  &__form-input {
    padding: 10px 10px 10px 0;
    width: 100%;
    font: $font-nav;
    font-weight: 400;
    color: $color-black;
    border-bottom: 2px solid $color-black;

    ::placeholder {
      font: $font-nav;
    }
  }
}

.datepicker {
  display: inline;
}

.popup--open {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
  transition: opacity 0.8s ease-in-out, transform 0.8s ease-in-out;
}

.popup--open .popup__content {
  transform: translateY(0);
}

.popup__close:hover {
  background-color: $color-orange;
  transition: background-color 0.2s ease-in-out;
}

.popup__close:hover .popup__close-line {
  background-color: $color-black;
  transition: background-color 0.2s ease-in-out;
}

.btn-popup-form {
  position: absolute;
  right: 0;
  bottom: 0;
  padding: 10px 119px;
  background-color: $color-black;
  color: $color-main;
  transition: background-color 0.2s ease-in-out, color 0.2s ease-in-out;
}

.btn-popup-form:hover {
  background-color: $color-orange;
  color: $color-black;
  transition: background-color 0.2s ease-in-out, color 0.2s ease-in-out;
}

@media (max-width:1439px) {

  .hero {

    &__title {
      font-size: 612px;
      line-height: 450px;
    }
  }

  .vector {
    background-size: 87%;
  }

  .popup {
    
    &__form {
      flex-direction: column;
      align-items: flex-start;
    }

    &__form-field {
      margin-bottom: 30px;
    }
  }

  .btn-popup-form {
    left: 0;
    right: unset;
  }
}

@media (max-width:1280px) {
  .hero {
    &__content {
      padding-left: 25px;
    }

    &__subtitle {
      padding-left: 17px;
    }

    &__title {
      font-size: 512px;
      line-height: 450px;
    }
  }

  .vector {
    top: -22px;
    height: 97%;
    background-size: 85%;
  }
}

@media (max-width:1024px) {
  .hero {
    &__subtitle {
      margin-bottom: 30px;
      font: $font-subtitle-1024;
    }

    &__title {
      font-size: 400px;
      line-height: 370px;
    }
  }
  .popup {
    &__title {
      margin-bottom: 70px;
      font-size: 94px;
      line-height: 94px;
    }
  }

  .vector {
    background-size: 70%;
  }
}

@media (max-width:768px) {
  .hero {

    &__container {
      padding-top: 80px;
      padding-bottom: 80px;
    }

    &__content {
      padding-left: 10px;
    }

    &__subtitle {
      margin-bottom: 25px;
      font: $font-subtitle-768;
    }

    &__title {
      font-size: 265px;
      line-height: 250px;
    }
  }

  .vector {
    top: -10px;
    background-size: 48%;
  }

  .popup {
    &__title {
      margin-bottom: 50px;
      font-size: 64px;
      line-height: 64px;
    }

    &__form {
      padding-bottom: 70px;
    }

    &__form-input-title {
      font: $font-nav-768;
      font-weight: 700;
    }

    &__form-input {
      font: $font-nav-768;
      font-weight: 400;
    }
  }
}

@media (max-width:500px) {
  .hero {
    &__container {
      padding-top: 40px;
      padding-bottom: 40px;
    }

    &__subtitle {
      margin-bottom: 15px;
      padding-left: 5px;
      font: $font-subtitle-500;
    }
    
    &__title {
      font-size: 178px;
      line-height: 165px;
    }
  }

  .vector {
    height: 102%;
    background-size: 33%;
  }

  .popup {
    &__body {
      padding: 40px 0;
    }

    &__title {
      margin-bottom: 30px;
      font-size: 44px;
      line-height: 44px;
    }

    &__close {
      top: 5px;
      width: 22px;
      height: 22px;
    }

    &__close-line {
      left: 6px;
      width: 10px;
    }
  }

  .btn-popup-form {
    padding: 10px 90px;
    width: 100%;
  }
}